.form-checkbox {
  cursor: pointer;
  position: relative;
  display: flex;
  align-items: center;
}

.form-checkbox label {
  margin: 0;
  margin-left: 9px;
  font-size: 14px;
}

.form-checkbox__mark {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 15px;
  height: 15px;
  border: 2px solid var(--color-dark-3);
}

.form-checkbox__icon {
  display: none;
  position: absolute;
  font-size: 6px;
  color: white;
}

.form-checkbox input {
  cursor: pointer;
  z-index: 5;
  position: absolute;
  opacity: 0;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.form-checkbox input:checked ~ .form-checkbox__mark {
  background-color: var(--color-dark-1);
  border-color: var(--color-dark-1);
}

.form-checkbox input:checked ~ .form-checkbox__mark .form-checkbox__icon {
  display: block;
}

.form-checkbox:hover input ~ .form-checkbox__mark {
  background-color: var(--color-dark-7);
}

.form-checkbox:hover input:checked ~ .form-checkbox__mark {
  background-color: var(--color-dark-1);
}

.form-radio {
  display: flex;
  align-items: center;
}

.form-radio .radio {
  cursor: pointer;
  position: relative;
  display: block;
}

.form-radio .radio__mark {
  height: 15px;
  width: 15px;
  border-radius: 100%;
  border: 2px solid var(--color-dark-3);
  display: flex;
  justify-content: center;
  align-items: center;
}

.form-radio .radio__icon {
  display: none;
  position: absolute;
  width: 7px;
  height: 7px;
  border-radius: 100%;
  background-color: var(--color-dark-1);
}

.form-radio .radio input {
  z-index: 5;
  position: absolute;
  opacity: 0;
  cursor: pointer;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.form-radio .radio input:checked ~ .radio__mark {
  border-color: var(--color-dark-1);
}

.form-radio .radio input:checked ~ .radio__mark .radio__icon {
  display: block;
}

.form-radio .radio:hover input ~ .radio__mark {
  background-color: rgba(0, 0, 0, 0.05);
}

.form-switch .switch {
  position: relative;
  width: 60px;
  height: 30px;
}

.form-switch .switch__slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  box-shadow: 0px 10px 50px rgba(13, 38, 59, 0.1);
  background-color: rgba(255, 255, 255, 0.2);
  transition: 0.3s;
  border-radius: 30px;
  border: 1px solid #e7e7ec;
}

.form-switch .switch__slider::before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 5px;
  bottom: 4px;
  background-color: var(--color-light-2);
  border-radius: 50%;
  transition: 0.3s;
}

.form-switch .switch input {
  z-index: 5;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}

.form-switch .switch input:checked + .switch__slider {
  background-color: var(--color-purple-1);
}

.form-switch .switch input:checked + .switch__slider::before {
  transform: translateX(29px);
  background-color: #ffffff;
}

