.is-loading {
  position: relative;
}

.is-loading:before {
  align-items: center;
  background-color: hsla(0, 0%, 100%, 0.6);
  content: "";
  display: flex;
  inset: 0;
  justify-content: center;
  position: absolute;
  z-index: 9998;
}

.is-loading:after {
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
  animation: lds-dual-ring 1.2s linear infinite;
  border-color: #e1501f transparent;
  border-radius: 50%;
  border-style: solid;
  border-width: 6px;
  content: "";
  display: block;
  height: 64px;
  left: 50%;
  margin: -32px 0 0 -32px;
  position: absolute;
  top: 50%;
  width: 64px;
  z-index: 9999;
}

@-webkit-keyframes lds-dual-ring {
  0% {
    transform: rotate(0);
  }

  to {
    transform: rotate(1turn);
  }
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0);
  }

  to {
    transform: rotate(1turn);
  }
}

